import React from 'react';
import PattyShowLove from '../components/patty-show-love'
import Standalone from '../components/layouts/standalone'
import SEO from '../components/seo'
import { graphql } from 'gatsby'

const Contact = ({ data }) => {
  return (
    <Standalone>
      <SEO
        title="Contact Expatty | Free business listings"
        description="Whether you find your business listed on Expatty already or want to get in touch for a free listing, stop by and say hi, or drop us a note."
        keywords={ [ 'free business listing', 'business directory', 'contact us', 'expat business', ...data.site.siteMetadata.commonKeywords ] }
      />
      <main>
        <div className="container-fluid">
          <h1 className={`title--header`}>Let's chat</h1>
          <div className="row">
            <div className="col-md mb-5">
              <p>Fill this out so we can learn more about you and your needs. Whether you find your business listed
                already or want to get in touch for a <strong>free</strong> listing, stop by and say hi, or drop us a note.</p>

              <p>Don't be a stranger! We always like to make new friends.</p>
            </div>
          </div>
          <div className="row align-items-center">
            <div className="col-md-3 text-center">
              <PattyShowLove />
            </div>
            <div className="col-md-6">
              <form action="https://formspree.io/info@expatty.com" method="POST">
                <div className="form-group">
                  <label>Your name</label>
                  <input className="form-control" type="text" name="name" placeholder="Name"
                         required />
                </div>
                <div className="form-group">
                  <label>Email address</label>
                  <input className="form-control" type="email" name="_replyto"
                         placeholder="Email address" required />
                </div>
                <div className="form-group">
                  <label>How can we help?</label>
                  <textarea rows="5" className="form-control" name="message"
                            placeholder="Your message" required />
                </div>
                <input className="btn btn-primary btn-lg" type="submit" value="Contact us" />
              </form>
            </div>
          </div>
        </div>
      </main>
    </Standalone>
  );
};

export default Contact;

export const pageQuery = graphql`
  query ContactUsQuery {
      site {
          siteMetadata {
              commonKeywords
          }
      }
  }
`;